.terminal-container {
}

.terminal-body {
	height: 200px;
	background-color: rgba(0, 0, 0, 0.8);
	color: aliceblue;
	overflow-y: scroll;
	overflow-x: hidden;
	padding: 8px;
	max-width: 100%;
}

.terminal-input {
	background-color: rgba(0, 0, 0, 0.8);
	color: aliceblue;
	border: none;
	width: calc(100% - 10px);
	padding-left: 8px;
	height: 24px;
}
